import {
  Filter as FilterIcon,
  Wallet,
  Boxes,
  ChevronDown,
  Palette,
  Scale,
  Tag,
  Sliders,
} from "lucide-react";

const Filter = ({
  selectedCategory,
  setSelectedCategory,
  categories,
  minPrice,
  maxPrice,
  setMinPrice,
  setMaxPrice,
  materials, // New prop for materials
  colors, // New prop for colors
  weightCapacities, // New prop for weight capacities
  userRatings, // New prop for user ratings
  selectedMaterials,
  setSelectedMaterials,
  selectedColors,
  setSelectedColors,
  selectedWeightCapacity,
  setSelectedWeightCapacity,
  onlyAvailable,
  setOnlyAvailable,
}) => {
  return (
    <aside className="w-full md:w-64 bg-white rounded-2xl shadow-lg border border-gray-200 p-6 text-right space-y-8 font-vazir">
      {/* عنوان اصلی فیلتر */}
      <div className="flex items-center justify-between border-b pb-2">
        <h3 className="text-lg font-bold text-gray-800 flex items-center gap-2">
          <FilterIcon className="w-5 h-5 text-blue-500" />
          فیلتر محصولات
        </h3>
        <ChevronDown className="text-gray-400" />
      </div>

      {/* دسته‌بندی‌ها */}
      <div>
        <h4 className="text-sm font-semibold text-gray-700 mb-3 flex items-center gap-2">
          <Boxes className="w-4 h-4 text-blue-400" />
          دسته‌بندی
        </h4>
        <ul className="space-y-2 text-sm text-gray-600">
          {categories.map((cat) => (
            <li key={cat}>
              <label className="cursor-pointer flex items-center gap-2 hover:text-blue-600 transition">
                <input
                  type="radio"
                  name="category"
                  value={cat}
                  checked={selectedCategory === cat}
                  onChange={() => setSelectedCategory(cat)}
                  className="accent-blue-600"
                />
                {cat}
              </label>
            </li>
          ))}
        </ul>
      </div>

      {/* فیلتر قیمت با Range */}
      <div>
        <h4 className="text-sm font-semibold text-gray-700 mb-3 flex items-center gap-2">
          <Wallet className="w-4 h-4 text-blue-400" />
          بازه قیمت
        </h4>
        <div className="flex items-center justify-between text-sm text-gray-600 mb-2">
          <span>{minPrice.toLocaleString("fa-IR")} تومان</span>
          <span>{maxPrice.toLocaleString("fa-IR")} تومان</span>
        </div>
        <input
          type="range"
          min="0"
          max="1000000000"
          step="1000000"
          value={minPrice}
          onChange={(e) => setMinPrice(Number(e.target.value))}
          className="w-full h-2 bg-blue-200 rounded-lg appearance-none cursor-pointer accent-blue-500"
        />
        <input
          type="range"
          min={minPrice}
          max="1000000000"
          step="1000000"
          value={maxPrice}
          onChange={(e) => setMaxPrice(Number(e.target.value))}
          className="w-full mt-3 h-2 bg-blue-200 rounded-lg appearance-none cursor-pointer accent-blue-500"
        />
      </div>

      {/* فیلترهای بیشتر (مثلاً موجودی یا برند) */}
      <div>
        <h4 className="text-sm font-semibold text-gray-700 mb-3 flex items-center gap-2">
          <Sliders className="w-4 h-4 text-blue-400" /> سایر فیلترها
        </h4>
        <label className="flex items-center gap-2 text-sm text-gray-600 hover:text-blue-600 cursor-pointer mb-2">
          <input
            type="checkbox"
            onChange={() => {
              setOnlyAvailable((a) => !a);
            }}
            className="accent-blue-600"
          />
          فقط کالاهای موجود
        </label>
      </div>

      {/* فیلتر جنس (Material) */}
      <div>
        <h4 className="text-sm font-semibold text-gray-700 mb-3 flex items-center gap-2">
          <Tag className="w-4 h-4 text-blue-400" /> جنس
        </h4>
        <ul className="space-y-2 text-sm text-gray-600">
          {materials.map((material) => (
            <li key={material}>
              <label className="cursor-pointer flex items-center gap-2 hover:text-blue-600 transition">
                <input
                  type="checkbox"
                  checked={selectedMaterials.includes(material)}
                  onChange={() => {
                    if (selectedMaterials.includes(material)) {
                      setSelectedMaterials(
                        selectedMaterials.filter((m) => m !== material)
                      );
                    } else {
                      setSelectedMaterials([...selectedMaterials, material]);
                    }
                  }}
                  className="accent-blue-600"
                />
                {material}
              </label>
            </li>
          ))}
        </ul>
      </div>

      {/* فیلتر رنگ (Color) */}
      <div>
        <h4 className="text-sm font-semibold text-gray-700 mb-3 flex items-center gap-2">
          <Palette className="w-4 h-4 text-blue-400" />
          رنگ
        </h4>
        <ul className="space-y-2 text-sm text-gray-600">
          {colors.map((color) => (
            <li key={color}>
              <label className="cursor-pointer flex items-center gap-2 hover:text-blue-600 transition">
                <input
                  type="checkbox"
                  checked={selectedColors.includes(color)}
                  onChange={() => {
                    if (selectedColors.includes(color)) {
                      setSelectedColors(
                        selectedColors.filter((c) => c !== color)
                      );
                    } else {
                      setSelectedColors([...selectedColors, color]);
                    }
                  }}
                  className="accent-blue-600"
                />
                {color}
              </label>
            </li>
          ))}
        </ul>
      </div>

      {/* فیلتر ظرفیت وزن (Weight Capacity) */}
      <div>
        <h4 className="text-sm font-semibold text-gray-700 mb-3 flex items-center gap-2">
          <Scale className="w-4 h-4 text-blue-400" /> ظرفیت وزن
        </h4>
        <ul className="space-y-2 text-sm text-gray-600">
          {weightCapacities.map((capacity) => (
            <li key={capacity}>
              <label className="cursor-pointer flex items-center gap-2 hover:text-blue-600 transition">
                <input
                  type="radio"
                  name="weightCapacity"
                  value={capacity}
                  checked={selectedWeightCapacity === capacity}
                  onChange={() => setSelectedWeightCapacity(capacity)}
                  className="accent-blue-600"
                />
                {capacity}
              </label>
            </li>
          ))}
        </ul>
      </div>
    </aside>
  );
};

export default Filter;
