import { Link } from "react-router-dom";
import { useState, useEffect } from "react";
import { motion } from "framer-motion";
import { ShoppingCart } from "lucide-react";
import { LazyLoadImage } from "react-lazy-load-image-component";
import "react-lazy-load-image-component/src/effects/blur.css";

export default function Product({
  id,
  images,
  name,
  category,
  price,
  quantity,
  isOnSale = false,
  originalPrice,
  description = "",
  imageUrl,
  onAddToCart,
}) {
  // Handle both images array and imageUrl
  const productImages =
    images?.length > 0
      ? images
      : imageUrl
      ? Array.isArray(imageUrl)
        ? imageUrl
        : [imageUrl]
      : [];

  const [imgLoaded, setImgLoaded] = useState(false);
  const isAvailable = quantity > 0;

  // Generate a clean slug for the URL
  const formattedSlug = encodeURIComponent(
    name?.replace(/\s+/g, "-").toLowerCase()
  );

  // Calculate discount percentage if on sale
  const discountPercentage =
    isOnSale && originalPrice > 0
      ? Math.round(((originalPrice - price) / originalPrice) * 100)
      : 0;

  // Format price with locale support
  const formatPrice = (priceValue) => {
    if (!priceValue) return "0";
    return priceValue.toLocaleString("fa-IR");
  };

  // Animation variants
  const cardVariants = {
    initial: { opacity: 0, y: 10 },
    animate: {
      opacity: 1,
      y: 0,
      transition: { duration: 0.3 },
    },
    hover: {
      scale: isAvailable ? 1.01 : 1,
      boxShadow:
        "0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04)",
      transition: { type: "spring", stiffness: 400, damping: 17 },
    },
    tap: {
      scale: isAvailable ? 0.98 : 1,
      transition: { type: "spring", stiffness: 400, damping: 17 },
    },
  };

  return (
    <motion.article
      initial="initial"
      animate="animate"
      whileHover="hover"
      whileTap="tap"
      variants={cardVariants}
      className="group relative bg-white rounded-xl shadow-lg hover:shadow-xl transition-all duration-300 w-full mx-auto h-[380px] border border-gray-100 overflow-hidden flex flex-col"
      itemScope
      itemType="https://schema.org/Product"
    >
      {/* Sold Out Overlay */}
      {!isAvailable && (
        <div className="absolute inset-0 bg-white/80 flex items-center justify-center z-20">
          <motion.span
            className="text-2xl font-bold text-red-600 rotate-12"
            aria-label="ناموجود"
          >
            ناموجود
          </motion.span>
        </div>
      )}

      <Link
        to={`/products/${formattedSlug}`}
        aria-label={`مشاهده جزئیات محصول ${name}`}
        title={name}
        className="block flex flex-col h-full"
      >
        {/* Badge Container */}
        <div className="absolute top-3 left-3 z-10 flex flex-col gap-2 rtl">
          {isOnSale && discountPercentage > 0 && (
            <div className="bg-red-500 text-white text-xs font-bold px-3 py-1.5 rounded-full shadow-md flex items-center justify-center">
              <span>{discountPercentage}٪</span>
              <span className="mr-1 text-xs">تخفیف</span>
            </div>
          )}
        </div>

        {/* Image Container - Fixed Height */}
        <div className="relative overflow-hidden bg-gray-50 h-48">
          {/* Loading skeleton */}
          <div
            className={`absolute inset-0 bg-gradient-to-r from-gray-100 to-gray-200 animate-pulse ${
              imgLoaded ? "opacity-0" : "opacity-100"
            } transition-opacity duration-300`}
          ></div>

          {/* Product Main Image */}
          <div className="relative w-full h-full overflow-hidden group">
            {/* First (default) image */}
            <img
              src={productImages[0]}
              alt={`تصویر محصول ${name}`}
              loading="lazy"
              className="absolute inset-0 w-full h-full object-contain object-center transition-opacity duration-700 ease-out opacity-100 group-hover:opacity-0"
            />

            {/* Second (hover) image */}
            {productImages.length > 1 && (
              <img
                src={productImages[1]}
                alt={`تصویر دیگر محصول ${name}`}
                loading="lazy"
                className="absolute inset-0 w-full h-full object-contain object-center transition-opacity duration-700 ease-out opacity-0 group-hover:opacity-100"
              />
            )}
          </div>

          {/* Image overlay with gradient */}
          <div className="absolute inset-0 bg-gradient-to-t from-black/20 via-transparent to-transparent opacity-0 group-hover:opacity-100 transition-opacity duration-300" />
        </div>

        {/* Content Container */}
        <div className="p-4 flex flex-col flex-grow text-right">
          <div className="mb-auto">
            <h3
              className="text-lg font-bold text-gray-800 mb-1 line-clamp-1 group-hover:text-blue-700 transition-colors duration-200"
              itemProp="name"
            >
              {name}
            </h3>

            {description && (
              <p
                className="text-xs md:text-sm text-gray-600 line-clamp-2 mb-2"
                itemProp="description"
              >
                {description}
              </p>
            )}

            {/* Category as a sub-heading if no description */}
            {!description && category && (
              <p className="text-xs text-gray-500 mb-2">{category}</p>
            )}
          </div>

          {/* Price container */}
          <div className="mt-auto pt-3 border-t border-gray-100">
            <div className="flex items-center justify-between">
              <span className="inline-flex items-center justify-center bg-blue-100 text-blue-700 text-xs px-2 py-1 rounded">
                {category || "محصول ویژه"}
              </span>

              <div className="text-left">
                {isOnSale && Number(originalPrice) > 0 && (
                  <div className="text-xs text-gray-400 line-through mb-0.5 ltr">
                    {formatPrice(Number(originalPrice))}{" "}
                    <span className="text-xs">تومان</span>
                  </div>
                )}
                <div className="flex items-center">
                  <span
                    className="text-base font-bold text-gray-900 ltr"
                    itemProp="offers"
                    itemScope
                    itemType="https://schema.org/Offer"
                  >
                    <span itemProp="price">{formatPrice(Number(price))}</span>
                    <meta
                      itemProp="availability"
                      content={
                        isAvailable
                          ? "https://schema.org/InStock"
                          : "https://schema.org/OutOfStock"
                      }
                    />
                    <meta itemProp="priceCurrency" content="IRR" />
                  </span>
                  <span className="text-xs font-medium text-gray-700 mr-1">
                    تومان
                  </span>
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* Quick add to cart button */}
        {isAvailable && (
          <div className="absolute bottom-4 left-4 right-4 opacity-0 group-hover:opacity-100 transition-opacity duration-300">
            <button
              className="bg-blue-600 hover:bg-blue-700 text-white text-xs font-medium py-2 px-4 rounded-full shadow-md transition-colors duration-200 flex items-center justify-center gap-1 w-full"
              aria-label={`افزودن ${name} به سبد خرید`}
              onClick={(e) => {
                e.preventDefault();
                e.stopPropagation();
                onAddToCart(id);
              }}
            >
              <ShoppingCart size={18} />
              افزودن به سبد خرید
            </button>
          </div>
        )}

        {/* Visual indicator that card is clickable */}
        <div className="absolute bottom-0 left-0 right-0 h-1 bg-blue-500 transform scale-x-0 group-hover:scale-x-100 transition-transform duration-300 origin-left"></div>
      </Link>
    </motion.article>
  );
}
