export default function ProductPageHero({ heroImage }) {
  return (
    <section className="relative h-72 md:h-96 overflow-hidden flex items-center">
      {/* Background Image */}
      <img
        src={heroImage}
        alt="تجهیزات باشگاهی - نقد و اقساط"
        className="absolute inset-0 w-full h-full object-cover"
        loading="eager"
        fetchPriority="high"
        decoding="async"
      />

      {/* Overlay */}
      <div className="absolute inset-0 bg-black bg-opacity-50" />

      {/* Text Content */}
      <div className="relative z-10 flex items-center px-6 w-full">
        <div className="max-w-6xl mx-auto text-white">
          <h1 className="text-3xl md:text-5xl font-bold mb-2">
            محصولات تجهیزات باشگاهی
          </h1>
          <p className="text-sm md:text-base opacity-90">
            انتخاب حرفه‌ای برای باشگاه حرفه‌ای
          </p>
        </div>
      </div>
    </section>
  );
}
