import { useEffect, useState } from "react";
import { motion, AnimatePresence } from "framer-motion";
import Header from "../ComponentChilds/Header";
import Footer from "../ComponentChilds/Footer";
import Filter from "../ComponentChilds/products/Filter";
import ProductPageHero from "../ComponentChilds/products/ProductPageHero";
import Product from "../ComponentChilds/products/Product";
import { Filter as FilterIcon } from "lucide-react";
import ProductCardsLoader from "../ComponentChilds/products/ProductCardsLoader";
import { useLocation, useSearchParams } from "react-router-dom";
import { Helmet } from "react-helmet";

import Pagination from "../ComponentChilds/Pagination";

const categories = ["همه", "هوازی", "بدنسازی", "پکیج", "لوازم جانبی"];
const materials = ["آهن", "پلاستیک", "استیل"];
const colors = ["مشکی", "سفید", "قرمز"];
const weightCapacities = [
  "زیر 100 کیلوگرم",
  "بین 100 تا 200 کیلوگرم",
  "بیش از 200 کیلوگرم",
];

const Products = ({ basketItemCount, onSetBasketItemCount }) => {
  const location = useLocation();
  const params = new URLSearchParams(location.search);
  const category = params.get("category"); // ✅ returns "همه"

  const [selectedCategory, setSelectedCategory] = useState(category || "همه");
  const [minPrice, setMinPrice] = useState(0);
  const [maxPrice, setMaxPrice] = useState(1000000000);
  const [onlyAvailable, setOnlyAvailable] = useState(false);
  const [selectedMaterials, setSelectedMaterials] = useState([]);
  const [selectedColors, setSelectedColors] = useState([]);
  const [selectedWeightCapacity, setSelectedWeightCapacity] = useState("");
  const [selectedUserRating, setSelectedUserRating] = useState("");
  const [showFilter, setShowFilter] = useState(false);
  const [allProducts, setAllProducts] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [searchParams, setSearchParams] = useSearchParams();
  const currentPage = parseInt(searchParams.get("page") || "1");

  useEffect(() => {
    if (category) {
      setSelectedCategory(category);
    }
  }, [category]);

  useEffect(() => {
    const API_URL =
      window.location.hostname === "localhost"
        ? "http://127.0.0.1:8000/api/products/"
        : "https://allmaxsport.com/api/products/";

    setIsLoading(true);
    async function fetchProducts() {
      try {
        const response = await fetch(API_URL);
        const data = await response.json();
        setAllProducts(data);
      } catch (e) {
      } finally {
        setIsLoading(false);
      }
    }
    fetchProducts();
  }, []);

  function handleAddToCart(id) {
    const shoppingBasket =
      JSON.parse(localStorage.getItem("shoppingBasket")) || [];
    let product = allProducts?.find((p) => p.id === id);

    const existingProduct = shoppingBasket.find(
      (item) => item.ProductId === product.id
    );

    if (existingProduct) {
      existingProduct.quantity += 1;
    } else {
      const productData = {
        id: shoppingBasket.length + 1,
        ProductId: product?.id,
        productName: product?.name,
        quantity: 1,
        price: Number(product?.price),
        imageUrl: product?.images[0],
        description: product?.description,
      };
      shoppingBasket.push(productData);
    }

    // Save the updated shopping basket to localStorage
    localStorage.setItem("shoppingBasket", JSON.stringify(shoppingBasket));
    onSetBasketItemCount(shoppingBasket.length);
  }

  const filtered = allProducts?.filter((product) => {
    const matchCategory =
      selectedCategory === "همه" || product.category === selectedCategory;
    const matchPrice = product.price >= minPrice && product.price <= maxPrice;
    const matchMaterial =
      selectedMaterials.length === 0 ||
      selectedMaterials.includes(product.material);
    const matchAvailability = !onlyAvailable || product?.stock > 0;
    const matchColor =
      selectedColors.length === 0 || selectedColors.includes(product?.color);
    const matchWeightCapacity =
      !selectedWeightCapacity ||
      product.weightCapacity === selectedWeightCapacity;

    return (
      matchCategory &&
      matchPrice &&
      matchMaterial &&
      matchColor &&
      matchWeightCapacity &&
      matchAvailability
    );
  });

  const itemsPerPage = 12;
  const sortedFiltered = filtered?.sort((a, b) => b.stock - a.stock);
  // Pagination logic
  const totalPages = Math.ceil(sortedFiltered.length / itemsPerPage);
  const indexOfLastItem = currentPage * itemsPerPage;
  const indexOfFirstItem = indexOfLastItem - itemsPerPage;
  const currentProducts = sortedFiltered.slice(
    indexOfFirstItem,
    indexOfLastItem
  );

  const drawerVariants = {
    hidden: { x: "100%", opacity: 0 },
    visible: { x: 0, opacity: 1, transition: { duration: 0.3 } },
    exit: { x: "100%", opacity: 0, transition: { duration: 0.3 } },
  };

  const backdropVariants = {
    hidden: { opacity: 0 },
    visible: { opacity: 1, transition: { duration: 0.2 } },
    exit: { opacity: 0, transition: { duration: 0.2 } },
  };

  return (
    <>
      <Helmet>
        <title>تجهیزات ورزشی باشگاه | آلمکس فروش تجهیزات بدنسازی و فیتنس</title>
        <meta
          name="description"
          content="فروش انواع تجهیزات ورزشی و بدنسازی برای باشگاه‌ها و حرفه‌ای‌ها. بهترین قیمت، کیفیت عالی و ارسال سریع. تجهیزات فیتنس، دمبل، تردمیل و سایر لوازم بدنسازی."
        />
        <meta
          name="keywords"
          content="تجهیزات ورزشی، تجهیزات باشگاه، تجهیزات فیتنس، دمبل، تردمیل، لوازم بدنسازی، فروش تجهیزات بدنسازی، تجهیزات بدنسازی حرفه‌ای"
        />
        <meta
          property="og:title"
          content="تجهیزات ورزشی و بدنسازی برای باشگاه‌ها"
        />
        <meta
          property="og:description"
          content="فروش انواع تجهیزات ورزشی و بدنسازی با بهترین قیمت و کیفیت. تجهیزات فیتنس، لوازم بدنسازی و مدارک حرفه‌ای."
        />
        <meta property="og:type" content="website" />
        <meta property="og:url" content="https://allmaxsport.com/products" />
        <meta
          property="og:image"
          content="https://yourwebsite.com/path-to-image.jpg"
        />
        <script type="application/ld+json">
          {`
        {
          "@context": "https://schema.org",
          "@graph": [
            {
              "@type": "WebPage",
              "@id": "https://allmaxsport.com#webpage",
              "url": "https://allmaxsport.com/products",
              "name": "تجهیزات ورزشی باشگاه | آلمکس فروش تجهیزات بدنسازی و فیتنس",
              "description": "فروش انواع تجهیزات ورزشی و بدنسازی برای باشگاه‌ها و حرفه‌ای‌ها. بهترین قیمت، کیفیت عالی و ارسال سریع. تجهیزات فیتنس، دمبل، تردمیل و سایر لوازم بدنسازی."
            },
            {
              "@type": "Organization",
              "@id": "https://allmaxsport.com#organization",
              "name": "AllMaxSport",
              "url": "https://allmaxsport.com",
              "logo": "https://allmaxsport.com/path-to-logo.jpg",
              "sameAs": [
                "https://instagram.com/allmaxsport",
                "https://facebook.com/allmaxsport"
              ]
            }
          ]
        }
        `}
        </script>
      </Helmet>

      <Header basketItemCount={basketItemCount} />
      <main className="bg-white text-right font-vazir">
        <ProductPageHero
          heroImage={
            "https://allmaxsport.com/Media/images/ftyq5kpsbslysjxokknp.avif"
          }
        />

        <div className="fixed bottom-0 inset-x-0 z-50 md:hidden shadow px-6 py-3 w-1/2">
          <div className="max-w-1/3 mx-auto">
            <button
              onClick={() => setShowFilter(true)}
              className="w-full inline-flex items-center justify-center gap-2 px-4 py-3 text-sm font-semibold bg-blue-600 text-white rounded-xl shadow hover:bg-blue-700 transition-all duration-200"
            >
              <FilterIcon className="w-4 h-4" />
              فیلتر محصولات
            </button>
          </div>
        </div>

        <section className="py-10 px-4">
          <div className="max-w-6xl mx-auto flex flex-col md:flex-row gap-8">
            {/* Sidebar */}
            <div className="md:w-64 flex-shrink-0 hidden md:block">
              <Filter
                selectedCategory={selectedCategory}
                setSelectedCategory={setSelectedCategory}
                categories={categories}
                materials={materials}
                selectedMaterials={selectedMaterials}
                setSelectedMaterials={setSelectedMaterials}
                colors={colors}
                selectedColors={selectedColors}
                setSelectedColors={setSelectedColors}
                weightCapacities={weightCapacities}
                selectedWeightCapacity={selectedWeightCapacity}
                setSelectedWeightCapacity={setSelectedWeightCapacity}
                selectedUserRating={selectedUserRating}
                setSelectedUserRating={setSelectedUserRating}
                minPrice={minPrice}
                maxPrice={maxPrice}
                setMinPrice={setMinPrice}
                setMaxPrice={setMaxPrice}
                setOnlyAvailable={setOnlyAvailable}
                onlyAvailable={onlyAvailable}
              />
            </div>
            {/* Mobile Filter Drawer with Animation */}
            <AnimatePresence>
              {showFilter && (
                <>
                  <motion.div
                    className="fixed inset-0 bg-black bg-opacity-40 backdrop-blur-sm z-40"
                    variants={backdropVariants}
                    initial="hidden"
                    animate="visible"
                    exit="exit"
                    onClick={() => setShowFilter(false)}
                  />

                  <motion.div
                    className="fixed top-0 bottom-0 right-0 w-80 max-w-full bg-white shadow-2xl z-50 p-4 overflow-y-auto rounded-l-2xl"
                    variants={drawerVariants}
                    initial="hidden"
                    animate="visible"
                    exit="exit"
                  >
                    <div className="flex justify-between items-center mb-4">
                      <h3 className="text-lg font-bold text-gray-800 flex items-center gap-2">
                        <FilterIcon className="w-5 h-5 text-blue-500" />
                        فیلتر محصولات
                      </h3>
                      <button
                        onClick={() => setShowFilter(false)}
                        className="text-gray-500 hover:text-blue-600 text-xl transition"
                      >
                        ✕
                      </button>
                    </div>

                    <Filter
                      selectedCategory={selectedCategory}
                      setSelectedCategory={setSelectedCategory}
                      categories={categories}
                      materials={materials}
                      selectedMaterials={selectedMaterials}
                      setSelectedMaterials={setSelectedMaterials}
                      colors={colors}
                      selectedColors={selectedColors}
                      setSelectedColors={setSelectedColors}
                      weightCapacities={weightCapacities}
                      selectedWeightCapacity={selectedWeightCapacity}
                      setSelectedWeightCapacity={setSelectedWeightCapacity}
                      selectedUserRating={selectedUserRating}
                      setSelectedUserRating={setSelectedUserRating}
                      minPrice={minPrice}
                      maxPrice={maxPrice}
                      setMinPrice={setMinPrice}
                      setMaxPrice={setMaxPrice}
                      setOnlyAvailable={setOnlyAvailable}
                      onlyAvailable={onlyAvailable}
                    />
                  </motion.div>
                </>
              )}
            </AnimatePresence>
            {/* Products Grid */}
            {/* Main Content */}
            <div className="flex-1 flex flex-col">
              <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6 gap-y-8">
                {isLoading ? (
                  <>
                    <ProductCardsLoader />
                    <ProductCardsLoader />
                    <ProductCardsLoader />
                  </>
                ) : currentProducts?.length > 0 ? (
                  currentProducts.map((product) => (
                    <Product
                      key={product?.id}
                      id={product?.id}
                      images={product?.images}
                      name={product?.name}
                      category={product?.category}
                      isOnSale={product?.isOnSale}
                      price={product?.price}
                      originalPrice={product?.originalPrice}
                      quantity={product?.stock}
                      imageUrl={product?.images}
                      onAddToCart={handleAddToCart}
                    />
                  ))
                ) : (
                  <div className="col-span-full flex flex-col items-center justify-center mt-20">
                    <p className="text-center text-gray-500 text-lg">
                      محصولی با این فیلتر پیدا نشد.
                    </p>
                  </div>
                )}
              </div>

              {totalPages > 1 && (
                <div className="flex justify-center mt-8">
                  <Pagination
                    currentPage={currentPage}
                    totalPages={totalPages}
                    onPageChange={(page) =>
                      setSearchParams({ page: page.toString() })
                    }
                  />
                </div>
              )}
            </div>
          </div>
        </section>
      </main>
      <Footer />
    </>
  );
};

export default Products;
